"use client";

import Image from "next/image";
import { motion } from "framer-motion";

export default function Header() {
  return (
    <header className="bg-white border-b border-gray-300 shadow-md py-2">
      <div className="max-w-7xl mx-auto px-6 grid grid-cols-4 items-center">

        {/* ======================================
            COLUMN 1 — BLF LOGO + TITLE + BUTTERFLY
        ======================================= */}
        <div className="flex items-center gap-3 relative">

          {/* BLF LOGO */}
          <Image
            src="/assets/img/logos/header1/BEAUTIFUL LIFE LOGO 2.jpg"
            alt="BLF Logo"
            width={70}
            height={70}
            className="rounded-full object-cover"
          />

          {/* TITLE */}
          <h1 className="text-4xl font-bold text-gray-900 whitespace-nowrap">
            Beautiful Life
          </h1>

          {/* BUTTERFLY — Fly RIGHT → LEFT + Face LEFT */}
          <motion.img
            src="/assets/img/logos/header1/butterfly (2).gif"
            alt="Butterfly"
            className="absolute top-[5px] right-[-40px]"
            style={{
              width: "75px",
              height: "75px",
              transform: "scaleX(1)", // FACE LEFT
            }}
            animate={{
              x: [20, -20, 20], // RIGHT → LEFT → RIGHT
              y: [0, -8, 0],
            }}
            transition={{
              duration: 3.5,
              repeat: Infinity,
              ease: "easeInOut",
            }}
          />
        </div>

        {/* ======================================
            COLUMN 2 — REGISTER / LOGIN / FORGOT
        ======================================= */}
        <nav className="flex items-center justify-center gap-4">

          {/* Register TAB — RED */}
          <a
            href="#"
            className="bg-red-600 text-white font-semibold px-4 py-2 rounded-lg shadow"
          >
            Register
          </a>

          {/* Login TAB — BLUE */}
          <a
            href="#"
            className="bg-blue-600 text-white font-semibold px-4 py-2 rounded-lg shadow"
          >
            Login
          </a>

          {/* Forgot Password — Normal black */}
          <a
            href="#"
            className="text-black font-medium whitespace-nowrap"
          >
            Forgot Password
          </a>
        </nav>

        {/* ======================================
            COLUMN 3 — FOF TEXT + PANTHER LOGO
        ======================================= */}
        <div className="flex flex-col items-center gap-2">
          <span className="text-gray-700 font-medium whitespace-nowrap">
            Friends of Friends (FOF)
          </span>

          {/* Panther Logo — 70×70 — Clickable */}
          <a href="#">
            <Image
              src="/assets/img/logos/header1/foflogo.jpg"
              alt="FOF Panther Logo"
              width={70}
              height={70}
              className="object-contain"
            />
          </a>
        </div>

        {/* ======================================
            COLUMN 4 — A2Z LOGO
        ======================================= */}
        <div className="flex justify-end">
          {/* A2Z Group Logo — 70×70 — Clickable */}
          <a href="#">
            <Image
              src="/assets/img/logos/header1/a2zgroups.jpg"
              alt="A2Z Group Companies Logo"
              width={70}
              height={70}
              className="object-contain"
            />
          </a>
        </div>

      </div>
    </header>
  );
}
